      <view class="subtitle">‰∏ÄÈîÆÂÆåÊàê‰∫ëÂáΩÊï∞ÈÉ®ÁΩ≤ÂíåÊï∞ÊçÆÂ∫ìÂàùÂßãÂåñ</view>
    </view>

    <!-- Á≥ªÁªüÁä∂ÊÄÅÊ£ÄÊü• -->
    <view class="section">
      <view class="section-title">üìä Á≥ªÁªüÁä∂ÊÄÅÊ£ÄÊü•</view>
      <view class="status-card">
        <view class="status-item">
          <view class="status-label">‰∫ëÂáΩÊï∞Áä∂ÊÄÅ</view>
