    </view>

    <!-- Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñ -->
    <view class="section">
      <view class="section-title">üóÑÔ∏è Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñ</view>
      <view class="action-card">
        <view class="action-desc">ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ìÈõÜÂêàÂíåÁ§∫‰æãÊï∞ÊçÆ</view>
        <view class="action-buttons">
          <button class="action-btn primary" @click="initDatabase(false)" :disabled="loading">
