        </view>
        <view class="no-logs" v-if="logs.length === 0">
          ÊöÇÊó†Êìç‰ΩúÊó•Âøó
        </view>
      </scroll-view>
      <button class="clear-btn" @click="clearLogs">Ê∏ÖÁ©∫Êó•Âøó</button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      systemStatus: {
        cloudFunction: { text: 'Êú™Ê£ÄÊü•', class: 'unknown' },
        database: { text: 'Êú™Ê£ÄÊü•', class: 'unknown' },
        sampleData: { text: 'Êú™Ê£ÄÊü•', class: 'unknown' }
      },
      logs: []
    }
  },
  
  onLoad() {
    this.addLog('info', 'Á≥ªÁªüÈÉ®ÁΩ≤ÁÆ°ÁêÜÈ°µÈù¢Â∑≤Âä†ËΩΩ');
    // Ëá™Âä®Ê£ÄÊü•Á≥ªÁªüÁä∂ÊÄÅ
    setTimeout(() => {
      this.checkSystemStatus();
    }, 1000);
  },
  
  methods: {
    // Ê∑ªÂä†Êó•Âøó
    addLog(type, message) {
      const now = new Date();
      const time = `${now.getHours().toString().padStart(2, '0')}:${now.getMinutes().toString().padStart(2, '0')}:${now.getSeconds().toString().padStart(2, '0')}`;
      
      this.logs.unshift({
        type,
        message,
        time
      });
      
      // ÈôêÂà∂Êó•ÂøóÊï∞Èáè
      if (this.logs.length > 50) {
        this.logs = this.logs.slice(0, 50);
      }
    },
    
    // Ê£ÄÊü•Á≥ªÁªüÁä∂ÊÄÅ
    async checkSystemStatus() {
      this.loading = true;
      this.addLog('info', 'ÂºÄÂßãÊ£ÄÊü•Á≥ªÁªüÁä∂ÊÄÅ...');
      
      try {
        // Ê£ÄÊü•‰∫ëÂáΩÊï∞Áä∂ÊÄÅ
        await this.checkCloudFunction();
        
        // Ê£ÄÊü•Êï∞ÊçÆÂ∫ìÁä∂ÊÄÅ
        await this.checkDatabase();
        
        // Ê£ÄÊü•Á§∫‰æãÊï∞ÊçÆ
        await this.checkSampleData();
        
        this.addLog('success', 'Á≥ªÁªüÁä∂ÊÄÅÊ£ÄÊü•ÂÆåÊàê');
      } catch (error) {
        this.addLog('error', `Á≥ªÁªüÁä∂ÊÄÅÊ£ÄÊü•Â§±Ë¥•: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    
    // Ê£ÄÊü•‰∫ëÂáΩÊï∞Áä∂ÊÄÅ
    async checkCloudFunction() {
      try {
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRooms',
            data: { pageSize: 1, pageNum: 1 }
          }
        });
        
        if (result.result.code === 0) {
          this.systemStatus.cloudFunction = { text: 'Ê≠£Â∏∏', class: 'success' };
          this.addLog('success', '‰∫ëÂáΩÊï∞ËøûÊé•Ê≠£Â∏∏');
        } else {
          this.systemStatus.cloudFunction = { text: 'ÂºÇÂ∏∏', class: 'error' };
          this.addLog('error', `‰∫ëÂáΩÊï∞ÂºÇÂ∏∏: ${result.result.message}`);
        }
      } catch (error) {
        this.systemStatus.cloudFunction = { text: 'Êú™ÈÉ®ÁΩ≤', class: 'error' };
        this.addLog('error', `‰∫ëÂáΩÊï∞Êú™ÈÉ®ÁΩ≤ÊàñÈÖçÁΩÆÈîôËØØ: ${error.message}`);
      }
    },
    
    // Ê£ÄÊü•Êï∞ÊçÆÂ∫ìÁä∂ÊÄÅ
    async checkDatabase() {
      try {
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'debugDatabase'
          }
        });
        
        if (result.result.code === 0) {
          this.systemStatus.database = { text: 'Ê≠£Â∏∏', class: 'success' };
          this.addLog('success', 'Êï∞ÊçÆÂ∫ìËøûÊé•Ê≠£Â∏∏');
        } else {
          this.systemStatus.database = { text: 'ÂºÇÂ∏∏', class: 'warning' };
          this.addLog('warning', `Êï∞ÊçÆÂ∫ìÂºÇÂ∏∏: ${result.result.message}`);
        }
      } catch (error) {
        this.systemStatus.database = { text: 'ÈîôËØØ', class: 'error' };
        this.addLog('error', `Êï∞ÊçÆÂ∫ìÊ£ÄÊü•Â§±Ë¥•: ${error.message}`);
      }
    },
    
    // Ê£ÄÊü•Á§∫‰æãÊï∞ÊçÆ
    async checkSampleData() {
      try {
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRooms',
            data: { pageSize: 10, pageNum: 1 }
          }
        });
        
        if (result.result.code === 0) {
          const total = result.result.data.total;
          if (total > 0) {
            this.systemStatus.sampleData = { text: `Â∑≤Êúâ${total}Êù°Êï∞ÊçÆ`, class: 'success' };
            this.addLog('success', `ÂèëÁé∞${total}Êù°ÊàøÈó¥Êï∞ÊçÆ`);
          } else {
            this.systemStatus.sampleData = { text: 'Êó†Êï∞ÊçÆ', class: 'warning' };
            this.addLog('warning', 'Êï∞ÊçÆÂ∫ì‰∏≠Ê≤°ÊúâÊàøÈó¥Êï∞ÊçÆ');
          }
        }
      } catch (error) {
        this.systemStatus.sampleData = { text: 'Ê£ÄÊü•Â§±Ë¥•', class: 'error' };
        this.addLog('error', `Á§∫‰æãÊï∞ÊçÆÊ£ÄÊü•Â§±Ë¥•: ${error.message}`);
      }
    },
    
    // ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ì
    async initDatabase(forceReset = false) {
      const action = forceReset ? 'ÈáçÁΩÆÊï∞ÊçÆÂ∫ì' : 'ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ì';
      
      uni.showModal({
        title: 'Á°ÆËÆ§Êìç‰Ωú',
        content: `Á°ÆÂÆöË¶Å${action}ÂêóÔºü${forceReset ? 'ËøôÂ∞ÜÂà†Èô§ÊâÄÊúâÁé∞ÊúâÊï∞ÊçÆÔºÅ' : ''}`,
        success: async (res) => {
          if (res.confirm) {
            await this.performDatabaseInit(forceReset);
          }
        }
      });
    },
    
    // ÊâßË°åÊï∞ÊçÆÂ∫ìÂàùÂßãÂåñ
    async performDatabaseInit(forceReset) {
      this.loading = true;
      this.addLog('info', `ÂºÄÂßã${forceReset ? 'ÈáçÁΩÆ' : 'ÂàùÂßãÂåñ'}Êï∞ÊçÆÂ∫ì...`);
      
      try {
        const result = await uniCloud.callFunction({
          name: 'db-init',
          data: { forceReset }
        });
        
        if (result.result.code === 0) {
          this.addLog('success', `Êï∞ÊçÆÂ∫ì${forceReset ? 'ÈáçÁΩÆ' : 'ÂàùÂßãÂåñ'}ÊàêÂäü`);
          uni.showToast({
            title: 'Êìç‰ΩúÊàêÂäü',
            icon: 'success'
          });
          // ÈáçÊñ∞Ê£ÄÊü•Áä∂ÊÄÅ
          setTimeout(() => {
            this.checkSystemStatus();
          }, 2000);
        } else {
          this.addLog('error', `Êï∞ÊçÆÂ∫ìÊìç‰ΩúÂ§±Ë¥•: ${result.result.message}`);
          uni.showToast({
            title: 'Êìç‰ΩúÂ§±Ë¥•',
            icon: 'error'
          });
        }
      } catch (error) {
        this.addLog('error', `Êï∞ÊçÆÂ∫ìÊìç‰ΩúÂºÇÂ∏∏: ${error.message}`);
        uni.showToast({
          title: 'Êìç‰ΩúÂºÇÂ∏∏',
          icon: 'error'
        });
      } finally {
        this.loading = false;
      }
    },
    
    // ÊµãËØïÂäüËÉΩ
    async testFunction(functionName) {
      this.loading = true;
      this.addLog('info', `ÊµãËØïÂäüËÉΩ: ${functionName}`);
      
      try {
        let testData = {};
        
        // Ê†πÊçÆ‰∏çÂêåÂäüËÉΩËÆæÁΩÆÊµãËØïÊï∞ÊçÆ
        switch (functionName) {
          case 'getRooms':
            testData = { pageSize: 10, pageNum: 1 };
            break;
          case 'getIncomeStatistics':
            testData = { type: 'monthly' };
            break;
          case 'getRoomOccupancyStatistics':
            testData = {};
            break;
          case 'debugDatabase':
            testData = {};
            break;
        }
        
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: functionName,
            data: testData
          }
        });
        
        if (result.result.code === 0) {
          this.addLog('success', `${functionName} ÊµãËØïÊàêÂäü`);
          console.log(`${functionName} ÁªìÊûú:`, result.result.data);
          
          uni.showModal({
            title: 'ÊµãËØïÊàêÂäü',
            content: `${functionName} ÂäüËÉΩÊ≠£Â∏∏ÔºåËØ¶ÁªÜÁªìÊûúËØ∑Êü•ÁúãÊéßÂà∂Âè∞`,
            showCancel: false
          });
        } else {
          this.addLog('error', `${functionName} ÊµãËØïÂ§±Ë¥•: ${result.result.message}`);
          uni.showModal({
            title: 'ÊµãËØïÂ§±Ë¥•',
            content: result.result.message,
            showCancel: false
          });
        }
      } catch (error) {
        this.addLog('error', `${functionName} ÊµãËØïÂºÇÂ∏∏: ${error.message}`);
        uni.showModal({
          title: 'ÊµãËØïÂºÇÂ∏∏',
          content: error.message,
          showCancel: false
        });
      } finally {
        this.loading = false;
      }
    },
    
    // ‰øÆÂ§çÊï∞ÊçÆ‰∏ç‰∏ÄËá¥
    async fixDataInconsistencies() {
      uni.showModal({
        title: 'Á°ÆËÆ§‰øÆÂ§ç',
        content: 'Á°ÆÂÆöË¶Å‰øÆÂ§çÊï∞ÊçÆ‰∏ç‰∏ÄËá¥ÈóÆÈ¢òÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            this.loading = true;
            this.addLog('info', 'ÂºÄÂßã‰øÆÂ§çÊï∞ÊçÆ‰∏ç‰∏ÄËá¥ÈóÆÈ¢ò...');
            
            try {
              const result = await uniCloud.callFunction({
                name: 'room-management',
                data: {
                  action: 'fixDataInconsistencies'
                }
              });
              
              if (result.result.code === 0) {
                const fixes = result.result.data.fixes_applied;
                this.addLog('success', `Êï∞ÊçÆ‰øÆÂ§çÂÆåÊàêÔºåÂÖ±‰øÆÂ§ç${fixes}‰∏™ÈóÆÈ¢ò`);
                uni.showToast({
                  title: `‰øÆÂ§ç‰∫Ü${fixes}‰∏™ÈóÆÈ¢ò`,
                  icon: 'success'
                });
              } else {
                this.addLog('error', `Êï∞ÊçÆ‰øÆÂ§çÂ§±Ë¥•: ${result.result.message}`);
                uni.showToast({
                  title: '‰øÆÂ§çÂ§±Ë¥•',
                  icon: 'error'
                });
              }
            } catch (error) {
              this.addLog('error', `Êï∞ÊçÆ‰øÆÂ§çÂºÇÂ∏∏: ${error.message}`);
              uni.showToast({
                title: '‰øÆÂ§çÂºÇÂ∏∏',
                icon: 'error'
              });
            } finally {
              this.loading = false;
            }
          }
        }
      });
    },
    
    // Ê∏ÖÁ©∫Êó•Âøó
    clearLogs() {
      this.logs = [];
      this.addLog('info', 'Êó•ÂøóÂ∑≤Ê∏ÖÁ©∫');
    }
  }
}
</script>

